import React, { Fragment } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Chip, TextField, MenuItem } from "@material-ui/core";
import {
  FacultyConfig,
  OutreachOptions,
  ResearchOptions
} from "../config/app-config";

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
    flexDirection: "column",
    "& > *": {
      margin: theme.spacing(1)
    }
  },
  chips: {
    display: "flex",
    flexWrap: "wrap"
  },
  chip: {
    margin: 2
  }
}));

function Personal(props) {
  const classes = useStyles();
  const { FormData, HandleChange, Errors } = props;
  const toProper = string => string.replace(/^\w/, c => c.toUpperCase());

  return (
    <Fragment>
      <form className={classes.root}>
        <TextField
          select
          onChange={HandleChange("Type")}
          error={Errors.Faculty !== ""}
          id="type"
          value={FormData.Type}
          label="Type"
          SelectProps={{
            MenuProps: {
              getContentAnchorEl: null,
              anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
              }
            }
          }}
          helperText="Registering Author's Faculty"
          variant="outlined"
          autoFocus={true}
          fullWidth={true}
        >
          <MenuItem key="course" value="course" dense>
            Course
          </MenuItem>
          <MenuItem key="thesis" value="thesis" dense>
            Thesis
          </MenuItem>
        </TextField>
        <TextField
          onChange={HandleChange("CourseCode")}
          error={Errors.CourseCode !== ""}
          id="course-code"
          value={FormData.CourseCode.toUpperCase()}
          label={toProper(FormData.Type) + " Code"}
          helperText={`See Student web for ${FormData.Type} code`}
          variant="outlined"
          fullWidth={true}
        />
        <TextField
          onChange={HandleChange("CourseName")}
          error={Errors.CourseName !== ""}
          id="course-name"
          value={FormData.CourseName}
          label={`${toProper(FormData.Type)} ${
            FormData.Type === "course" ? "Name" : "Title"
          }`}
          helperText={
            Errors.CourseName !== ""
              ? Errors.CourseName
              : `${FormData.Type === "course" ? "Name" : "Title"} of 
              ${FormData.Type}`
          }
          variant="outlined"
          fullWidth={true}
        />
        <TextField
          onChange={HandleChange("CourseResponsible")}
          error={Errors.CourseCode !== ""}
          id="course-responsible"
          value={FormData.CourseResponsible}
          label={`${
            FormData.Type === "course"
              ? "Course Responsible"
              : "Main Supervisor"
          }`}
          helperText={`Name of ${
            FormData.Type === "course"
              ? "Person Responsible for the course"
              : "Main Supervisor"
          }`}
          variant="outlined"
          fullWidth={true}
        />
        <TextField
          select
          onChange={HandleChange("Faculty")}
          error={Errors.Faculty !== ""}
          id="faculty"
          value={FormData.Faculty.toUpperCase()}
          label="Faculty"
          SelectProps={{
            MenuProps: {
              getContentAnchorEl: null,
              anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
              }
            }
          }}
          helperText={`Faculty responsible for the ${FormData.Type}`}
          variant="outlined"
          fullWidth={true}
        >
          {FacultyConfig.map(option => (
            <MenuItem key={option.value} value={option.value} dense>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
        <TextField
          select
          onChange={HandleChange("RelatedFaculties")}
          SelectProps={{
            renderValue: selected => (
              <div className={classes.chips}>
                {selected.map(value => (
                  <Chip
                    key={value}
                    label={
                      FacultyConfig.filter(item => item.value === value)[0]
                        .label
                    }
                    className={classes.chip}
                  />
                ))}
              </div>
            ),
            multiple: true,
            MenuProps: {
              getContentAnchorEl: null,
              anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
              }
            }
          }}
          value={FormData.RelatedFaculties}
          error={Errors.RelatedFaculties !== ""}
          id="related-faculties"
          label="Collaborating Faculties"
          helperText="Other collaborating faculties"
          variant="outlined"
          fullWidth={true}
        >
          {FacultyConfig.map(option => (
            <MenuItem key={option.value} value={option.value} dense>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
        <TextField
          select
          SelectProps={{
            MenuProps: {
              getContentAnchorEl: null,
              anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
              }
            }
          }}
          onChange={HandleChange("CourseType")}
          error={Errors.CourseType !== ""}
          name="course-type"
          id="course-type"
          value={FormData.CourseType}
          label={`${toProper(FormData.Type)} Type`}
          helperText={`Type of the ${FormData.Type}`}
          variant="outlined"
          fullWidth={true}
        >
          {ResearchOptions.map(option => (
            <MenuItem key={option.value} value={option.value} dense>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
        <TextField
          select
          SelectProps={{
            MenuProps: {
              getContentAnchorEl: null,
              anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
              }
            }
          }}
          onChange={HandleChange("Outreach")}
          error={Errors.Outreach !== ""}
          name="outreach"
          id="outreach"
          value={FormData.Outreach}
          label="Outreach"
          helperText={`Communicated with decision maker about the ${FormData.Type}?`}
          variant="outlined"
          fullWidth={true}
        >
          {OutreachOptions.map(option => (
            <MenuItem key={option.value} value={option.value} dense>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
      </form>
    </Fragment>
  );
}

export default Personal;
